<template>
  <div style="background: #eff3f6;padding-bottom: 30px">
    <div v-show="loading">
      <div class="loading">
        <img src="../assets/images/loading.gif" alt="">      </div>
    </div>
    <div v-show="limits">
      <mt-popup v-model="popupVisible2" position="top" class="mint-popup-2" :modal="false">
        <p ><img src="../assets/images/fail.png" alt=""><span></span></p>
      </mt-popup>
      <mt-actionsheet
        :actions="actions"
        v-model="sheetVisible" class="sheet">
      </mt-actionsheet>
      <!--<mt-actionsheet-->
        <!--:actions="actions2"-->
        <!--v-model="sheetVisible2" class="sheet">-->
      <!--</mt-actionsheet>-->
    <div class="background"></div>
    <div class="msgList device-info">
      <ul>
        <li>
        	<input type="hidden" v-model="applyInfo.assetsNum"/>
          <span></span><i>{{applyInfo.assetsNum}}</i>
        </li>
        <li>
        	<input type="hidden" v-model="applyInfo.assetsName"/>
          <span></span><i>{{applyInfo.assetsName}}</i>
        </li>
        <li>
        	<input type="hidden" v-model="applyInfo.deptId"/>
          <span></span><i>{{applyInfo.assetsDeptName}}</i>
        </li>
        <li>
        	<input type="hidden" v-model="applyInfo.assetsSpec"/>
          <span></span><i>{{applyInfo.assetsSpec}}</i>
        </li>
      </ul>
    </div>
    <div class="background"></div>
    <div class="msgList">
      <ul>
        <li  @click="sheet">
          <span></span><i :class="{urgencyColor:isColor}" class="ichoose">{{applyInfo.urgentLevel | urgentLevel}}</i>
          <img src="../assets/images/jr.png" alt="" class="choose">
        </li>
      </ul>
    </div>
    <div class="background"></div>
    <div class="uploadImg" style="width: 100%;overflow: hidden">
      <span></span>
      <uploader   :imgArr.sync="imgList"  :src="'/api/upload/'" :urgentLevel="applyInfo.urgentLevel" :faultDesc="applyInfo.faultDesc"></uploader>
    </div>
    <div class="background"></div>
    <div class="decribe uploadImg">
      <span></span>
      <textarea placeholder="300" v-model.trim="applyInfo.faultDesc" @keyup="key" @paste="key"></textarea>
      <span class="number"><i>{{applyInfo.faultDesc.length}}</i>/300</span>
    </div>
    <button-com  @click.native="applyDone"  :class="{disabled:isdiable ,active:isActive,btn:!isActive}" ></button-com>
 </div>
    <div v-show="nolimits">
      <div class="noresult">

        <img src="../assets/images/noresult.png" alt="">
        <div>{{content}}</div>
      </div>
      <button-com class="btnLeave" @click.native="goHome"  :class="{active:isActive,btn:!isActive}"></button-com>
    </div>
  </div>
</template>
<script>
  import { Toast } from 'mint-ui'
  import Vue from 'vue'
  import api from '../service/api.js'
  import ButtonCom from '../components/Button'
  import wxInit  from  '../wechat/weixin.js'
  import wx from 'weixin-js-sdk'
  import uploader from '../components/uploader'
  export default{
    data(){
      return{
        loading:true,
        limits:false,
        nolimits:false,
        isdiable:false,
        isActive:false,
        sheetVisible :false,
        sheetVisible2 :false,
        popupVisible2:false,
        isColor:false,
        actions:[],
        actions2:[],
        assetsImgsrc:'',
        imgList:[],
        content:'',
        applyInfo:{
          faultDesc:''
        }
      }
    },
    components:{ButtonCom,uploader},
    created (){
    	let _this=this;
      if(Vue.ls.get("urgentLevel")){
        this.isColor=true
      }

    	api.getDeviceDetail({
    	   data: {id:this.$route.query.id},
          callBack:function(res){
        	if(res.code!=200 || !res.data){
            setTimeout(()=>{
              _this.loading=false
              _this.limits=false
              _this.content=''
              _this.nolimits=true
            },1000)
          }else if(res.data && res.data.statusName==''){
            setTimeout(()=>{
              _this.loading=false
              _this.limits=false
              _this.nolimits=true
              _this.content=''
            },1000)
          }else if(res.data && res.data.statusName==''){
            setTimeout(()=>{
              _this.loading=false
              _this.limits=false
              _this.nolimits=true
              _this.content=''
            },1000)

          }else if(res.code==200){
            setTimeout(()=>{
            _this.loading=false
            _this.limits=true
            _this.nolimits=false
            },1000)
          }
        },
        _this:this,
        isApply:'isApply',
        method:'get'
    	}).then(data=>{
    	  this.applyInfo={
    	  	assetsNum:data.assetsNum,  //
    	  	assetsName:data.assetsName, //
          assetsDeptName:data.deptName,     //
    	  	assetsSpec:data.assetsSpec,   //
    	  	assetsId:data.assetsId,       //id
    	  	faultDesc:Vue.ls.get("faultDesc"),                 //
    	  	assetsImg:this.imgList,       //
          urgentLevel:Vue.ls.get("urgentLevel")?Vue.ls.get("urgentLevel"):'',            //
    	  	tenantId:Vue.ls.get("useInfo").tenantId ,//id
          deptId:data.deptId, //id
          factoryNum:data.factoryNum,//
          factoryName:data.factoryName,//
          startUseDate:data.startUseDate//
    	  }
    	})
    },
    updated(){
    	 Vue.ls.set("newApply","isNewApply");	
    },
    methods:{
      key(){
        if(this.applyInfo.faultDesc.length>=300){
          this.applyInfo.faultDesc=this.applyInfo.faultDesc.substr(0,300)
        }
      },
      goHome(){
        this.isActive=true
        this.$router.push('/tabbar')
      },
      applyDone(){
        this.isActive=true

        if(this.isdiable)return
//        id
        if(!this.applyInfo.deptId){
          Toast({
            message: '',
            position: 'center',
            duration: 1500
          })
          setTimeout(()=>{
            this.isActive=false
          },1000)
        }
        if(this.applyInfo.urgentLevel==''){
          Toast({
            message: '',
            position: 'center',
            duration: 1500
          })
          setTimeout(()=>{
            this.isActive=false
          },1000)
        }else{

          if(this.applyInfo.faultDesc==''){
            Toast({
              message: '',
              position: 'center',
              duration: 1500
            })
            setTimeout(()=>{
              this.isActive=false
            },1000)
          }
        }
        if(this.applyInfo.urgentLevel !=''&&this.applyInfo.faultDesc!='' && this.applyInfo.deptId){
        	//
        
        	this.imgList=this.imgList.length!=0 ?  this.imgList : Vue.ls.get("currImgArr")
        	this.applyInfo.assetsImg=(this.imgList.length && this.imgList.join(',')) ||''
        	this.isdiable=true
          api.newApply({
            method:'post',
            _this:this,
            callBack:function(){
            	this.isdiable=false
            },
            data:this.applyInfo
          }).then(response => {
            this.$router.push({ path: '/applyDone', query: { id: response.id,newApply:1}})
          })
        }

      },
      sheet(){
        this.sheetVisible = true
      },
      chooseImg(){

      }
    },
    mounted(){
      this.actions=[
        {name:'',method:()=>{
            this.isColor=true,
            this.applyInfo.urgentLevel=4
      }},
        {name:'',method:()=>{
            this.isColor=true,
            this.applyInfo.urgentLevel=3
        }},
        {name:'',method:()=>{
            this.isColor=true,
            this.applyInfo.urgentLevel=2
        }},
        {name:'',method:()=>{
            this.isColor=true,
            this.applyInfo.urgentLevel=1
        }}]
    },
    watch: {
      popupVisible2(val) {
        if (val) {
          setTimeout(() => {
            this.popupVisible2 = false;
          }, 2000);
        }
      },
    applyInfo:{
      handler(newValue, oldValue) {
        if(newValue.faultDesc.length>=300){
          this.applyInfo.faultDesc=newValue.faultDesc.substr(0,300)
        }
      },
      deep: true
    }
    }
  }
</script>
<style scoped lang="scss">
  @import "../assets/scss/reset.scss";
  @import "../assets/scss/my-mixin.scss";
  .background{
    height:pxToRem(20px);
    background: #eff3f6;
  }
  .msgList{
    @include MsgList();
    font-size: pxToRem(28px);
  }
  .msgList li:last-child{
    border-bottom: 0;
  }
  .msgList i{
    color: #bebebe;
  }

  .msgList i.urgencyColor{
    color: #666;
  }
  .msgList span{
    color: #333;
    display: inline-block;
    margin-right: pxToRem(70px);
    vertical-align: middle;
  }
  .msgList i{
    color: #666;
    width: 68%;
  }
  .msgList .ichoose{
    color: #999;
  }
  .choose{
    width: pxToRem(15px);
    height:pxToRem(28px);
    margin-top: pxToRem(30px);
    margin-right: pxToRem(30px);
    float: right;
  }
  .uploadImg{
    font-size: pxToRem(28px);
    padding:  pxToRem(30px) 0 pxToRem(30px) pxToRem(30px);
    background: #fff;
    min-height: pxToRem(275px) ;
    span{
      margin-right: pxToRem(70px);
      float: left;
    };
    img{
      width: pxToRem(150px);
      height: pxToRem(150px);
    }
  }
  .decribe{
    margin-bottom: pxToRem(60px) ;
    border-bottom: 1px solid #e5e5e5;
  }
  .decribe textarea{
    width: pxToRem(510px);
    height: pxToRem(210px);
    resize: none;
    color: #666;
    line-height:pxToRem(48px) ;
  }
  .decribe .number{
    margin-right:  pxToRem(30px);
    margin-top: pxToRem(-30px) ;
    float: right;
    color: #bebebe;
  }
.device-info {
  	i{
  		width:pxToRem(500px);
			text-overflow: ellipsis;
			overflow: hidden;
			white-space: nowrap;
			display: inline-block;
			vertical-align: middle;
  	}
  }


</style>




